.App {
  font-family: sans-serif;
  text-align: center;
}

.containerBox {
  border: 1px white solid;
  font-family: monospace;
  color: white;
  padding: 10px;
  background: rgba(0, 0, 0, 0.5);
}

.textBox {
  padding: 20px;
  font-size: 14px;
  line-height: 1.6;
}

.apyBox {
  border: 1px solid white;
}

.apyIcon {
  max-width: 25px;
  max-height: auto;
  top: 5px;
}

.splashBox {
  padding: 20px;
  text-align: center;
  font-size: 14px;
  color: white;
}

.blue:hover {
  background: blue;
}

.noDecoration {
  text-decoration: none;
  color: white;
}

.background {
  font-family: monospace;
}

.backgroundMobile {
  width: 100%;
}

@keyframes zoom {
  0% {
    background-size: 100%;
    background-position: center;
  }
  50% {
    background-size: 150%;
  }
  100% {
    background-size: 100%;
    background-position: center;
  }
}

.backgroundPicWrapper {
  max-width: 100%;
  max-height: 100%;
  position: absolute;
  z-index: -1;
  transform: scale(1.5);
  -ms-transform: scale(1.5); /* IE 9 */
  -moz-transform: scale(1.5); /* Firefox */
  -webkit-transform: scale(1.5); /* Safari and Chrome */
  -o-transform: scale(1.5); /* Opera */
  transition-duration: 2s;
}

.projectPic {
  width: 30%;
  height: 30%;
  flex-direction: column;
  flex-wrap: wrap;
}

.sitePic {
  width: 30%;
  height: 30%;
  flex-direction: column;
  flex-wrap: wrap;
}

.msu {
  max-width: 30%;
  max-height: 30%;
}

.icon {
  max-width: 50px;
  max-height: 50px;
}

.projectDescription {
  max-width: 50%;
  max-height: 50%;
  padding-left: 20px;
}

.center {
  display: flex;
  justify-content: center;
  overflow: hidden;
  font-size: 14px;
}

.center img {
  transition: transform 0.5s ease;
}

.center:hover {
  background-color: blue;
}

.profilePic {
  max-width: 30%;
  max-height: 30%;

  margin: 20px;
}

@-webkit-keyframes snowflakes-fall {
  0% {
    top: -10%;
  }
  100% {
    top: 100%;
  }
}
@-webkit-keyframes snowflakes-shake {
  0% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
  50% {
    -webkit-transform: translateX(80px);
    transform: translateX(80px);
  }
  100% {
    -webkit-transform: translateX(0px);
    transform: translateX(0px);
  }
}
@keyframes snowflakes-fall {
  0% {
    top: -10%;
  }
  100% {
    top: 100%;
  }
}
@keyframes snowflakes-shake {
  0% {
    transform: translateX(0px);
  }
  50% {
    transform: translateX(80px);
  }
  100% {
    transform: translateX(0px);
  }
}
.snowflake {
  position: fixed;
  top: -10%;
  z-index: 9999;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  cursor: default;
  -webkit-animation-name: snowflakes-fall, snowflakes-shake;
  -webkit-animation-duration: 10s, 3s;
  -webkit-animation-timing-function: linear, ease-in-out;
  -webkit-animation-iteration-count: infinite, infinite;
  -webkit-animation-play-state: running, running;
  animation-name: snowflakes-fall, snowflakes-shake;
  animation-duration: 10s, 3s;
  animation-timing-function: linear, ease-in-out;
  animation-iteration-count: infinite, infinite;
  animation-play-state: running, running;
}
.snowflake:nth-of-type(0) {
  left: 1%;
  -webkit-animation-delay: 0s, 0s;
  animation-delay: 0s, 0s;
}
.snowflake:nth-of-type(1) {
  left: 10%;
  -webkit-animation-delay: 1s, 1s;
  animation-delay: 1s, 1s;
}
.snowflake:nth-of-type(2) {
  left: 20%;
  -webkit-animation-delay: 6s, 0.5s;
  animation-delay: 6s, 0.5s;
}
.snowflake:nth-of-type(3) {
  left: 30%;
  -webkit-animation-delay: 4s, 2s;
  animation-delay: 4s, 2s;
}
.snowflake:nth-of-type(4) {
  left: 40%;
  -webkit-animation-delay: 2s, 2s;
  animation-delay: 2s, 2s;
}
.snowflake:nth-of-type(5) {
  left: 50%;
  -webkit-animation-delay: 8s, 3s;
  animation-delay: 8s, 3s;
}
.snowflake:nth-of-type(6) {
  left: 60%;
  -webkit-animation-delay: 6s, 2s;
  animation-delay: 6s, 2s;
}
.snowflake:nth-of-type(7) {
  left: 70%;
  -webkit-animation-delay: 2.5s, 1s;
  animation-delay: 2.5s, 1s;
}
.snowflake:nth-of-type(8) {
  left: 80%;
  -webkit-animation-delay: 1s, 0s;
  animation-delay: 1s, 0s;
}
.snowflake:nth-of-type(9) {
  left: 90%;
  -webkit-animation-delay: 3s, 1.5s;
  animation-delay: 3s, 1.5s;
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

/* Opera < 12.1 */
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media only screen and (max-width: 600px) {
  .center {
    display: flex;
    flex-wrap: wrap;
  }

  .projectPic {
    flex: 1;
    max-width: 50%;
  }

  .projectDescription {
    max-width: 80%;
  }
}

@media only screen and (min-width: 1080px) {
  .background {
    background-image: url(https://ripple.com/static/57bc9009f068cfbf5b6f01e4bc3d28a5/8e93e/shubham-dhage-T9rKvI3N0NM-unsplash-scaled.jpg);
    animation: zoom 60s infinite;
    -webkit-animation: zoom 60s infinite;
    background-size: 100%;

    background-repeat: no-repeat;
    background-attachment: fixed;
  }

  .blue:hover {
    background: none;
  }

  .center:hover {
    background: none;
  }
}

